@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;700&display=swap);
.clock {
    color: #FFF;
    display: flex;
    width: 560px;
    justify-content: space-between;
}

.clock__time {
    letter-spacing: -10px;
    font-size: 200px;
    font-weight: 700;
    line-height: 146px;
    margin-right: 30px;
}

.clock__time-zone {
    align-self: end;
    font-size: 40px;
    line-height: 30px;
}


.greeting-msg {
	display: flex;
	margin-bottom: 30px;
}

.greeting-msg__text {
	color: #fff;
	text-transform: uppercase;
	font-size: 16px;
	letter-spacing: 3px;
	margin-left: 16px;
}

.user-location {
    color: #FFF;
    font-size: 24px;
    margin-top: 30px;
}

.pill-button {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #FFF;
    width: 146px;
    border-radius: 100px;
    padding: 8px 8px 8px 14px;
    cursor: pointer;
}

.chevron circle {
    transition: fill .2s linear;
}

.pill-button:hover .chevron circle {
    fill: #999999;
}

.pill-button__text {
    font-size: 16px;
    opacity: .5;
    letter-spacing: 5px;
    font-weight: 700;
    text-transform: uppercase;
}

.chevron {
    transition: transform .35s linear;
}

.chevron.rotate {
    transform: rotate(180deg);
}


.quote {
	display: flex;
}

.quote__text {
	color: #fff;
	font-size: 18px;
	line-height: 28px;

	width: 500px;
	margin-right: 30px;
}

.quote__author {
	font-weight: 700;
    margin-top: 28px;
}

.quote__author span {
    font-weight: 400;
    margin-right: 10px;
}

.quote__author span {
    font-weight: 400;
    margin-right: 12px;
}

.quote__refresh-icon {
	margin-top: 6px;
    cursor: pointer;
}

.quote__refresh-icon:hover path {
    opacity: 1;
}
.backdrop {
	position: absolute;
	height: 100vh;
	width: 100vw;
	top: 0;
	left: 0;
    pointer-events: none;
}

.backdrop__image {
	position: absolute;
	height: 100vh;
	width: 100%;
	z-index: -2;
	background-size: cover;
}

.backdrop__opacity-layer {
	position: absolute;
	height: 100vh;
	width: 100%;
	z-index: -1;
    background-color: black;
    opacity: .35;
}

.backdrop__image--night {
	background-image: url(/clock_app/static/media/night.4b665d8f.jpg);
}
.backdrop__image--day {
	background-image: url(/clock_app/static/media/morning.c994685d.jpg);
}

.dashboard {
    grid-gap: 30px;
    gap: 30px;
	display: grid;
	grid-template-columns: 5fr 3fr;
	height: 50vh;
	padding-top: 75px;
	padding-left: 80px;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
}

.dashboard--night {
	background-color: hsla(0, 0%, 0%, 0.45);
	color: #fff;
}
.dashboard--day {
    color: #303030;
	background-color: rgba(255, 255, 255, 0.6);
}

.dashboard h3 {
    font-size: 56px;
    font-weight: 700;
    margin-top: 4px;
}

html {
    box-sizing: border-box;
    font-size: 16px;
  }
  
  *, *:before, *:after {
    box-sizing: border-box;
  }
  
  body, h1, h2, h3, h4, h5, h6, p, ol, ul, li {
    margin: 0;
    padding: 0;
    font-weight: normal;
  }
  
  ol, ul {
    list-style: none;
  }
  
  img {
    max-width: 100%;
    height: auto;
  }

#root {
	height: 100vh;
	font-family: "Inter", sans-serif;
}

.app {
	height: 100vh;
    overflow: hidden;
}

.widgets {
    height: 100vh;
    padding: 80px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.container {
    transition: transform .6s ease-in;
}

.container.open {
    transform: translateY(-50vh);
}

.flex-grp {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
